.game {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.game-title {
  font-size: 36px;
  font-weight: 700;
  color: #FFF;
  display: block;
  margin-bottom: 20px;
  text-align: center;
}

.game-info {
  margin-top: 16px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.move-button {
  width: 200px;
  background-color: #27AE60;
  border-width: 0;
  border-radius: 5px;
  color: #FFF;
  font-weight: 200;
  margin-bottom: 10px;
  padding: 10px 20px;
  text-decoration: none;
}

.status {
  margin-bottom: 10px;
}

.status.winner {
  font-weight: 700;
  color: #FFE347;
}